.quote{

    
    &__text{
        font-size: 36px;
        margin: 100px auto;
        border-left: #F7DF94 8px solid; 
        padding: 0 80px;
        text-align: justify;
        max-width: 800px;
        animation: fadein 0.6s;
    }

    &__author{
        font-weight: 700;
        font-size: 24px;
        text-align: left;
        margin: 10px auto;
        max-width: 800px;
        color: #4F4F4F;
    }

    &__author-bold{
        font-size: 36px;
    }

    &__genre{
        text-align: left;
        font-size: 16px;
        color: #828282;
    }

    &__button{
        max-width: 800px;
        margin: auto;
        padding: 20px;
        border-radius: 4px;
        cursor: pointer;

        &:hover{
            background-color: #ebebeb;
            transition: 0.2s;
            .quote__arrow{
                opacity: 1;
                transition: 0.2s;
            }
        }
    }

    &__return{
        cursor: pointer;
        width: 180px;
        align-self: flex-start;
        font-size: 16px;
        .icon{
            position: relative;
            top: 6px;
            margin-right: 10px;
        }
    }

    &__arrow{
        opacity: 0;
        margin: 10px;
        float: right;
        position: relative;
        top: -50px;
        color: #4F4F4F
    }
}

@media (max-width: 1000px) {
    .quote{
        padding: 10px;
        &__text{
            margin: 80px auto;
            border-left: #F7DF94 8px solid; 
            padding: 0 20px;
            font-size: 24px;
        }
    }
}

@media (max-width: 350px) {
    .quote__text{
        font-size: 20px;
    }
}

@keyframes fadein {
    0% {
        position: relative;
        top: 10px;
        opacity: 0;
    }

    100% {
        position: relative;
        top: 0;
        opacity: 1;
    }
}