@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500;700&display=swap);
body{height:100%;margin:0}

.quote__text{font-size:36px;margin:100px auto;border-left:#F7DF94 8px solid;padding:0 80px;text-align:justify;max-width:800px;-webkit-animation:fadein 0.6s;animation:fadein 0.6s}.quote__author{font-weight:700;font-size:24px;text-align:left;margin:10px auto;max-width:800px;color:#4F4F4F}.quote__author-bold{font-size:36px}.quote__genre{text-align:left;font-size:16px;color:#828282}.quote__button{max-width:800px;margin:auto;padding:20px;border-radius:4px;cursor:pointer}.quote__button:hover{background-color:#ebebeb;transition:0.2s}.quote__button:hover .quote__arrow{opacity:1;transition:0.2s}.quote__return{cursor:pointer;width:180px;-webkit-align-self:flex-start;align-self:flex-start;font-size:16px}.quote__return .icon{position:relative;top:6px;margin-right:10px}.quote__arrow{opacity:0;margin:10px;float:right;position:relative;top:-50px;color:#4F4F4F}@media (max-width: 1000px){.quote{padding:10px}.quote__text{margin:80px auto;border-left:#F7DF94 8px solid;padding:0 20px;font-size:24px}}@media (max-width: 350px){.quote__text{font-size:20px}}@-webkit-keyframes fadein{0%{position:relative;top:10px;opacity:0}100%{position:relative;top:0;opacity:1}}@keyframes fadein{0%{position:relative;top:10px;opacity:0}100%{position:relative;top:0;opacity:1}}

.App{text-align:center;font-family:'Raleway', sans-serif;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;overflow:hidden;width:100wh}.random{color:#4F4F4F;-webkit-align-self:flex-end;align-self:flex-end;margin:10px 5%;cursor:pointer}@media (max-width: 350px){.random{-webkit-align-self:inherit;align-self:inherit}}.random .icon{position:relative;top:6px}

